adro"},[_c('li',[_c('div',{staticClass:"lista-ol--cuadro__vineta"},[_c('span',[_vm._v("b")])]),_c('b',[_vm._v("Mercado real")])])])])]),_c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-9"},[_c('p',[_vm._v("Este segmento de mercado incluye a los compradores actuales de un producto o usuarios de un servicio que ya está en el mercado. Para el caso de uno nuevo, incluye los nichos a los que la empresa va a dirigir el producto inicialmente, teniendo en cuenta la investigación realizada. Con la identificación del mercado real comienza el proceso publicitario y los datos que de este segmento se derivan, guían las primeras configuraciones del mensaje que finalmente se verá en medios. Para identificar claramente al consumidor con el producto, es importante que las características de los modelos de los anuncios tengan información muy precisa del mercado.")]),_c('p',[_vm._v("Variables para la caracterización del mercado real.")]),_c('p',[_vm._v("La caracterización de este segmento se hace aplicando las variables demográficas, esto permite que su identificación sea más objetiva gracias a que las variables a utilizar son medibles y pueden generar datos estadísticos muy útiles para el diseño de estrategias:")])]),_c('div',{staticClass:"col-lg-3 col-8 col-sm-6"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/img07.png"),"alt":""}})])])])
